<template>
	<div class="joke">
		<header-nav></header-nav>
		<joke-rec></joke-rec>
		<footer-nav v-bind:class="{'joke':curr}"></footer-nav>
	</div>
</template>

<script>
	import HeaderNav from '../../components/header.vue'
	import FooterNav from '../../components/footer.vue'
	import JokeRec from '../../components/jokerec.vue'
	export default{
		components : {
			HeaderNav,
			FooterNav,
			JokeRec
		},
		data(){
			return{
				'curr':true
			}
		}
	}
</script>

<style>
</style>


