<template>
	<div class="wrap">
		<header-nav></header-nav>
		<index-focus></index-focus>
		<index-rec></index-rec>
		<footer-nav v-bind:class="{'home':curr}"></footer-nav>
	</div>
</template>

<script>
	import HeaderNav from '../../components/header.vue'
	import FooterNav from '../../components/footer.vue'
	import IndexFocus from '../../components/indexfocus.vue'
	import IndexRec from '../../components/indexrec.vue'
	export default{
		components : {
			HeaderNav,
			FooterNav,
			IndexFocus,
			IndexRec
		},
		data(){
			return{
				'curr':true
			}
		}
	}
</script>

<style>
</style>


