            	//手势滑动时，手势坐标不断变化，取最后一点的坐标为最终的终点坐标
                this.ex = touch.pageX
                this.cx = (this.sx - this.ex) > 0 ? (-(this.sx - this.ex)) : (this.sx - this.ex)
                //console.log("滑动的距离："+this.ex)
