import Vue from 'vue'
import Router from 'vue-router'

/*template*/
import HelloWorld from '@/components/HelloWorld'
import Index from '@/views/index/index'
import Life from '@/views/life/life'
import Joke from '@/views/joke/joke'
import Login from '@/views/login/login'
import Register from '@/views/register/register'
import Member from '@/views/member/member'
import Newsdetail from '@/views/index/newsdetail'
import Oil from '@/views/life/oil'
import Delivery from '@/views/life/delivery'
import Idcard from '@/views/life/idcard'
import Chat from '@/views/chat/chat'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/life',
      name: 'Life',
      component: Life
    },
    {
      path: '/joke',
      name: 'Joke',
      component: Joke
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/member',
      name: 'Member',
      component: Member
    },
    {
    	path: '/newsdetail',
    	name: 'Newsdetail',
    	component: Newsdetail
    },
    {
    	path: '/oil',
    	name: 'Oil',
    	component: Oil
    },
    {
    	path: '/delivery',
    	name: 'Delivery',
    	component: Delivery
    },
    {
    	path: '/idcard',
    	name: 'Idcard',
    	component: Idcard
    },
    {
    	path: '/chat',
    	name: 'Chat',
    	component: Chat
    }
  ]
})



